.projects {
  color: white;
  font-family: "Oxygen", sans-serif;

  margin-bottom: 4rem;

  .projects__navbar {
    font-family: "Ubuntu", sans-serif;
    font-size: 1.6rem;
    margin: 3rem auto;
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    cursor: pointer;

    .projects__navbar-active {
      color: rgb(146, 146, 255);
    }

    :hover {
      color: rgb(144, 144, 255);
      transition: all 0.3s ease;
      transform: scale(1.1);
    }
  }

  .row {
    animation: slideTop 2s ease forwards;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;

    .projectCard {
      padding: 1rem;

      background-image: url(../images/gif.gif);
      background-size: cover;
      background-position: center;
      border-radius: 20%;
      margin: 3rem;
      width: 33%;
      min-height: 40rem;
      .wrapper {
        color: #ffffff;
        margin: auto;
        padding: 0.5rem 1.5rem;
        width: 80%;
        text-align: center;
        line-height: 2.3rem;
        min-height: 25rem;

        h4 {
          font-family: "oneday", sans-serif;
          font-size: 2.2rem;
        }

        p {
          font-size: 1.2rem;
        }

        img {
          width: 100%;
          margin-top: 1rem;
          margin-left: auto;
          margin-bottom: 2rem;
          border-radius: 10%;
        }

        a {
          text-decoration: none;
          color: #ffffff;
          font-size: 1.5rem;
          margin: 0 2rem;
          display: inline-block;

          &:hover {
            color: rgb(113, 113, 200);
            transition: all 0.3s ease-in-out;
            transform: scale(1.3);
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1229px) {
  .projectCard {
    width: 80vw !important;
  }
}
@media screen and (max-width: 1080px) {
  .projects__navbar {
    div {
      padding: 2rem;
    }
  }
}
@media screen and (max-width: 785px) {
  .projectCard {
    min-width: 30rem !important;
  }
}

@media screen and (max-width: 529px) {
  .projects__navbar {
    text-align: center;
  }
}
@media screen and (max-width: 400px) {
  .row {
    .projectCard {
      h4 {
        font-size: 1.7rem !important;
      }
      p {
        font-size: 1.2rem !important;
      }
      .links {
        a {
          font-size: 1.5rem !important;
          line-height: 1.7rem;
          padding-top: 1rem;
        }
      }
    }
  }
}
